	import moveVerify from "@/components/moveVerify.vue"
	import {  
        mapMutations  
    } from 'vuex';
	
	export default{
		components: {
		        "move-verify":moveVerify
		},
		data(){
			return {
				mobile: '',
				password: '',
				verifyCode:'',
				loginName: '',
				logining: false,
				phoneExistance: true,
				nameExistance: true
			}
		},
		onLoad(){
			console.log('login页面onLoad');
		},
		methods: {
			...mapMutations(['login']),
			/* 校验结果回调函数 */
			verifyResult(res){
				console.log(res);
				this.resultData = res;
			},
			/* 校验插件重置 */
			verifyReset(){
				this.$refs.verifyElement.reset();
			
				/* 删除当前页面的数据 */
				this.resultData = {};
			},
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack();
			},
			checkExistPhone(){
				if(this.$refs.verifyElement.phoneExistance == true){
					this.phoneExistance = true;
				}else{
					this.$api.msg("手机号有误或未注册");
					this.phoneExistance = false;
				}
			},
			checkExistName(){
				console.log(this.loginName);
				/* 这里判断数据库中是否存在该用户名 */
				uni.request({
					url: this.apiServer+'/customer/checkNameExistance',
					method: 'POST',
					dataType: "json",
					data: { 
					   "loginName": this.loginName,
					},
					success: (res) => {
						const result = res.data
						console.log(result)
						if(result != 0){
							this.nameExistance = true;
						}else{
							this.$api.msg("用户名有误或未注册")
							this.nameExistance = false;
						}
				    }
				});
			},
			toGuidance(){
				uni.navigateTo({
					url: '/pages/public/guidance',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			toAgreement(){
				uni.navigateTo({
					url: '/pages/public/agreement',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			async updateCustomerPwd(){
				if(this.nameExistance == true && this.phoneExistance == true){
					//if(verifyCode验证正确)
					uni.request({
						url: this.apiServer+'/customer/updateCustomerPwd',
						method: 'POST',
						dataType: "json",
						data: { 
						   "password": this.password,
						   "mobile": this.mobile
						},
						success: (res) => {
							console.log(result)
							const result = res.data
							
							this.$api.msg("找回成功，请登录")
							setTimeout(() => {
								uni.navigateTo({
									url: '/pages/public/loginByName',
								});
							}, 3000)
					    }
					});
				}
				
			}
		},

	}
